import { useEffect, useState, useReducer } from "react";

import VendorConnection from "../../models/VendorConnection";

import { CONFIGURATIONS_LOAD_INITIAL_STATE } from "../../consts/configurations/configurationsActions";
import { FETCH_IS_LOADING, FETCH_SET_DATA, FETCH_THROW_ERROR } from "../../consts/fetchActions";

import { fetchStatusReducer, initialState } from "../../reducers/fetchReducer";
import useInteractiveFetchStatus from "../useInteractiveFetchStatus";
import { useSaveAdvancedConfiguration } from "./useConfiguration";

const VENDOR_DISPLAY_NAME_MAPPING = {
  einsteinSalesforceDestination: "Einstein Salesforce Destination",
  bulkRestoreSalesforceDestination: "Bulk Restore Salesforce Destination"
};

export const useInterativeSaveConfigurations = sourceOfTrueDispatch => {
  const [validityState, validateVendors] = useVendorValidity();
  const [configState, saveConfiguration] = useSaveAdvancedConfiguration();
  const [
    ,
    { spinnerIsRunning, spinnerIsStopped, successNotification, errorNotification }
  ] = useInteractiveFetchStatus();

  useEffect(() => {
    if (validityState.isLoading === true || configState.isLoading === true) spinnerIsRunning();
    else if (configState.isLoading === false || validityState.error !== null) spinnerIsStopped();
  }, [configState.isLoading, validityState.isLoading, validityState.error]);

  useEffect(() => {
    if (configState.success === true) successNotification("Configuration saved successfully");
  }, [configState.success]);

  useEffect(() => {
    if (configState.error !== null) errorNotification("Could not save configuration");
  }, [configState.error, errorNotification]);

  useEffect(() => {
    if (validityState.error !== null) {
      const errorMessages = `Invalid credentials: ${validityState.error
        .map(error => VENDOR_DISPLAY_NAME_MAPPING[error])
        .join(", ")}`;
      errorNotification(errorMessages);
    }
  }, [validityState.error]);

  useEffect(() => {
    if (configState.success === true && configState.data !== null) {
      sourceOfTrueDispatch({
        type: CONFIGURATIONS_LOAD_INITIAL_STATE,
        payload: configState.data
      });
    }
  }, [configState.success, configState.data, sourceOfTrueDispatch]);

  return [validityState, { saveConfiguration, validateVendors }];
};

const useVendorValidity = () => {
  const [credentials, validateVendors] = useState(null);
  const [fetchState, fetchDispatch] = useReducer(fetchStatusReducer, initialState);

  useEffect(() => {
    if (credentials === null) return;
    fetchDispatch({
      type: FETCH_IS_LOADING,
      payload: { isLoading: true }
    });

    (async () => {
      let vendorResults = {};

      for (const credentialsSet of credentials) {
        const vendorConnection = new VendorConnection(credentialsSet);
        const validationResult = await vendorConnection.validate();
        const status = validationResult.status;
        if (status === "failed") vendorResults[credentialsSet.label] = null;
        else vendorResults[credentialsSet.label] = validationResult;
      }

      const invalids = Object.keys(vendorResults).filter(vendor => vendorResults[vendor] === null);

      if (invalids.length > 0) {
        fetchDispatch({
          type: FETCH_THROW_ERROR,
          payload: { error: invalids }
        });
      } else {
        fetchDispatch({
          type: FETCH_SET_DATA,
          payload: { data: vendorResults }
        });
      }

      fetchDispatch({ type: FETCH_IS_LOADING, payload: { isLoading: false } });
    })();
  }, [credentials, validateVendors]);

  return [fetchState, validateVendors];
};
