import React, { Fragment } from "react";
import { useHistory } from "react-router-dom";
import {
  Card,
  DataTable,
  DataTableColumn,
  Button,
  IconSettings,
  ScopedNotification
} from "@salesforce/design-system-react";

import CloudConfig from "../../../../models/CloudConfig";
import DetailsCell from "./DetailsCell";
import { CONFIGURATION_PATH } from "../../../../consts/addresses";

const ConfigurationSummary = ({ config }) => {
  const history = useHistory();
  const redirect = path => history.push(CONFIGURATION_PATH + path);

  const editButton = (
    <Button label="Update Credentials" onClick={() => redirect("/basic-credentials")} />
  );
  const editAdvConfigButton = (
    <Button label="Edit Advanced Configuration" onClick={() => redirect("/advanced-config")} />
  );

  const cloudConfig = new CloudConfig(config);
  const tableData = cloudConfig.configSummaryData.map(datum => ({ ...datum, id: datum["system"] }));

  const table = (
    <DataTable items={tableData} id={"config-summary"}>
      <DataTableColumn key="system" label="System" property="system" />
      <DataTableColumn key="details" label="Details" property="details">
        <DetailsCell />
      </DataTableColumn>
    </DataTable>
  );

  return (
    <Fragment>
      <Card heading="Configuration Summary" headerActions={editButton}>
        {table}
      </Card>
      <Card heading="Advanced Configuration" headerActions={editAdvConfigButton}>
        <IconSettings iconPath="/assets/icons">
          <ScopedNotification theme="dark">
            <p>Only advanced users should edit these settings.</p>
          </ScopedNotification>
        </IconSettings>
      </Card>
    </Fragment>
  );
};

export default ConfigurationSummary;
