import { useEffect } from "react";

import { useConfigurationSave } from "./useConfiguration";
import useVendorValidity from "./useVendorValidity";
import useInteractiveFetchStatus from "../useInteractiveFetchStatus";
import CloudConfig from "../../models/CloudConfig";

import { useConfigurationsContext } from "../../contexts/configurationsContext";
import { useSalesforceConnectionContext } from "../../contexts/salesforceConnectionContext";
import { useElasticsearchContext } from "../../contexts/elasticsearchContext";
import { useCloudStorageContext } from "../../contexts/cloudStorageContext";

import VENDOR_DISPLAY_NAME_MAPPING from "../../consts/configurations/vendorDisplayNameMapping.json";

const chooseProvider = state => {
  const provider = state.provider;
  return state[provider];
};

const areAllValid = validationResults => {
  const results = Object.values(validationResults);
  return results.length > 0 && results.every(result => result === "success");
};

const invalidVendors = validationResults => {
  return Object.keys(validationResults).filter(vendor => validationResults[vendor] === "failed");
};

const useSaveBasicCredentials = () => {
  const saveConfiguration = useConfigurationSave()[1];
  const [configuration] = useConfigurationsContext();
  const [dataSourcesState] = useSalesforceConnectionContext();
  const [elasticState] = useElasticsearchContext();
  const [storageState] = useCloudStorageContext();
  const [{ vendorValidity, fetchState }, validateVendors] = useVendorValidity();
  const {
    successNotification,
    errorNotification,
    spinnerIsRunning,
    spinnerIsStopped
  } = useInteractiveFetchStatus()[1];

  const dataSource = [chooseProvider(dataSourcesState)][0];
  const elastic = [chooseProvider(elasticState)][0];
  const storage = [chooseProvider(storageState)][0];
  let cloudConfig;

  const buildConfig = () => {
    cloudConfig = new CloudConfig(configuration);
    cloudConfig.sourceDataSource = dataSource.salesforceSource;
    cloudConfig.elastic = elastic;
    cloudConfig.storage = storage;

    return cloudConfig;
  };

  useEffect(() => {
    (async () => {
      if (!fetchState.isLoading && fetchState.success) {
        if (areAllValid(vendorValidity)) {
          try {
            const cloudConfig = buildConfig();
            await saveConfiguration(cloudConfig.configuration);
            successNotification("Configuration saved successfully");
          } catch {
            errorNotification("Could not save configuration");
          }
        } else {
          const invalidVendorArray = invalidVendors(vendorValidity);
          const displayList = invalidVendorArray.map(vendor => VENDOR_DISPLAY_NAME_MAPPING[vendor]);
          errorNotification(
            `Invalid credentials: ${displayList.join(
              ", "
            )}. Please provide valid credentials and try again.`
          );
        }
      } else if (fetchState.isLoading === false) {
        errorNotification("Error connecting to server. Please try again later.");
      }
    })();
  }, [vendorValidity, fetchState.isLoading, fetchState.success, fetchState.error]);

  useEffect(() => {
    if (fetchState.isLoading) {
      spinnerIsRunning();
    } else {
      spinnerIsStopped();
    }
  }, [fetchState.isLoading]);

  const onSave = async () => {
    const cloudConfig = buildConfig();

    await validateVendors([
      {
        type: "elastic",
        credentials: cloudConfig.elastic
      },
      {
        type: "storage",
        credentials: cloudConfig.storage
      },
      {
        type: "salesforce",
        credentials: cloudConfig.sourceDataSource
      }
    ]);
  };

  return onSave;
};

export default useSaveBasicCredentials;
