import {
  CONFIGURATIONS_LOAD_INITIAL_STATE,
  CONFIGURATIONS_SAVE
} from "../../consts/configurations/configurationsActions";
import InvalidActionTypeError from "../../models/errors/InvalidActionTypeError";

const configurationsReducer = (state, action) => {
  switch (action.type) {
    case CONFIGURATIONS_LOAD_INITIAL_STATE:
      return action.payload;
    default:
      throw new InvalidActionTypeError();
  }
};

export default configurationsReducer;
