import React from "react";
import { Button } from "@salesforce/design-system-react";
import useSaveAdvancedConfig from "../../../../hooks/configurations/useSaveAdvancedConfig";

export const SaveButton = ({ formRef }) => {
  const saveAdvancedConfig = useSaveAdvancedConfig();

  return (
    <Button
      type="submit"
      label={"Save"}
      onClick={() => {
        if (formRef.current.checkValidity() === true) saveAdvancedConfig();
      }}
    />
  );
};

export default SaveButton;
