import React from "react";
import _ from "lodash";

import EmptyLandingPage from "./EmptyLandingPage";
import ConfigurationSummary from "./configurationSummary";
import INITIAL_STATE from "../../../consts/configurations/initialState";
import { useConfigurationsContext } from "../../../contexts/configurationsContext";

const StartPageSwitcher = () => {
  const [configuration] = useConfigurationsContext();

  if (!configuration || _.isEqual(configuration, INITIAL_STATE)) {
    return <EmptyLandingPage />;
  } else {
    return <ConfigurationSummary config={configuration} />;
  }
};

export default StartPageSwitcher;
