import Cloud from "../components/cloud";
import Scenarios from "../components/cloud/dedup/Scenarios";
import Scenario from "../components/cloud/dedup/Scenario";
import Duplicates from "../components/cloud/dedup/duplicates/Duplicates";

import Configurations from "../components/cloud/configurations";
import StartPageSwitcher from "../components/cloud/configurations/StartPageSwitcher";
import EmptyLandingPage from "../components/cloud/configurations/EmptyLandingPage";
import BasicCredentialsForm from "../components/cloud/configurations/basicCredentialsForm";
import AdvancedConfig from "../components/cloud/configurations/advancedConfig";

import { CLIENT_CLOUD_PATH, CONFIGURATION_PATH, DEDUP_PATH } from "./addresses";

const routes = [
  {
    path: CLIENT_CLOUD_PATH,
    component: Cloud,
    routes: [
      {
        exact: true,
        path: CONFIGURATION_PATH,
        component: Configurations,
        routes: [
          {
            exact: true,
            path: CONFIGURATION_PATH,
            component: StartPageSwitcher
          },
          {
            exact: true,
            path: `${CONFIGURATION_PATH}/basic-credentials`,
            component: BasicCredentialsForm
          },
          {
            exact: true,
            path: `${CONFIGURATION_PATH}/advanced-config`,
            component: AdvancedConfig
          },
          {
            exact: true,
            path: `${CONFIGURATION_PATH}/empty-landing-page`,
            component: EmptyLandingPage
          }
        ]
      },
      {
        exact: true,
        path: `${DEDUP_PATH}/scenarios`,
        component: Scenarios
      },
      {
        exact: true,
        path: `${DEDUP_PATH}/scenarios/:scenario_id`,
        component: Scenario
      },
      {
        exact: true,
        path: `${DEDUP_PATH}/scenarios/new`,
        component: Scenario
      },
      {
        exact: true,
        path: `${DEDUP_PATH}/duplicates/:scenario_id`,
        component: Duplicates
      }
    ]
  }
];

export default routes;
