import React from "react";
import { Button } from "@salesforce/design-system-react";
import useSaveBasicCredentials from "../../../../hooks/configurations/useSaveBasicCredentials";

export const BasicCredentialsSaveButton = ({ formRef }) => {
  const saveCredentials = useSaveBasicCredentials();

  return (
    <Button
      type="submit"
      label={"Save"}
      onClick={() => {
        if (formRef.current.checkValidity() === true) saveCredentials();
      }}
    />
  );
};

export default BasicCredentialsSaveButton;
