import { useContext, useEffect } from "react";
import {
  QUICK_START_FORM_SET_PROVIDER,
  QUICK_START_FORM_SET_VALUE
} from "../../consts/configurations/configurationsActions";

export const useContextInitialValue = (dispatch, defaultValue) => {
  useEffect(() => {
    if (defaultValue.provider) {
      dispatch({
        type: QUICK_START_FORM_SET_PROVIDER,
        payload: { provider: defaultValue.provider }
      });
      dispatch({
        type: QUICK_START_FORM_SET_VALUE,
        payload: { ...defaultValue }
      });
    }
  }, [defaultValue]);
};

export const useStateContext = context => {
  const state = useContext(context);

  if (state === undefined) {
    throw new Error("useStateContext must be called inside CloudStorageProvider");
  }

  return state;
};

export const useDispatchContext = context => {
  const dispatch = useContext(context);

  if (dispatch === undefined) {
    throw new Error("useDispatchContext must be called inside CloudStorageProvider");
  }

  return dispatch;
};
