import { useReducer, useEffect } from "react";
import VendorConnection from "../../models/VendorConnection";
import vendorValidityReducer from "../../reducers/configurations/vendorValidityReducer";
import { UPDATE_VENDOR_VALIDITY } from "../../consts/configurations/vendorValidityActions";
import fetchReducer from "../../reducers/fetchReducer";
import asyncAction from "../../lib/asyncAction";
import useInteractiveFetchStatus from "../useInteractiveFetchStatus";

const useVendorValidity = () => {
  const [vendorValidity, validationDispatch] = useReducer(vendorValidityReducer, {});
  const [fetchState, fetchDispatch] = useReducer(fetchReducer, {
    isLoading: null,
    error: null,
    status: null
  });
  const { spinnerIsRunning, spinnerIsStopped } = useInteractiveFetchStatus()[1];

  useEffect(() => {
    if (fetchState.isLoading === true) {
      spinnerIsRunning("Validating vendor credentials");
    } else if (fetchState.isLoading === false) {
      spinnerIsStopped();
    }
  }, [fetchState.isLoading]);

  const validateVendors = async credentials => {
    (async () => {
      const validate = async () => {
        const vendorResults = {};

        const validations = credentials.map(async credentialsSet => {
          const vendorConnection = new VendorConnection(credentialsSet);
          return vendorConnection.validate().then(result => {
            const status = result.status === "failed" ? "failed" : "success";
            vendorResults[vendorConnection.type] = status;
          });
        });

        await Promise.all(validations);

        validationDispatch({
          payload: { ...vendorResults },
          type: UPDATE_VENDOR_VALIDITY
        });
      };
      await asyncAction(validate, fetchDispatch);
    })();
  };

  return [{ vendorValidity, fetchState }, validateVendors];
};

export default useVendorValidity;
