import React from "react";
import { DataTableCell } from "@salesforce/design-system-react";

const DetailsCell = ({ children, ...props }) => {
  const grid = Object.keys(children).map(property => {
    return <p key={property}>{`${property}: ${children[property]}`}</p>;
  });
  return <DataTableCell {...props}>{grid}</DataTableCell>;
};
DetailsCell.displayName = DataTableCell.displayName;

export default DetailsCell;
